	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapitre 9 - Nouvelle famille","texte":{"childMarkdownRemark":{"html":"<p>Cela fait maintenant une bonne semaine que les unités d’interventions de GenCorp et la Guilde se sont mises à la recherche de La Légion et des Black Eagles, mais ils restent introuvables. Les membres de la guilde sont épuisés.</p>\n<p>Wamien : C’est bon les amis, rentrez à la base. On doit accueillir convenablement Angélique aussi.</p>\n<p>J’ai le sentiment que nous n’arriverons pas à les trouver de si tôt. Mais si on ne les trouve pas le plus tôt possible, ils vont avoir le temps de préparer quelque chose de diabolique. Clara prépare les plats avec Sarah et Gaïa pour la fête d’arrivée des nouveaux, Angélique, Gabriel, Ezekiel et Thomas.</p>\n<p>Angélique se promenait avec Gabriel et faisait retrouvaille puisqu’ils se connaissaient un peu à l’école. Gabriel semblait plus qu’heureux de la revoir. Certains s’amusaient à faire des allusions sur le peut-être futur des deux jeunes s’ils sortaient ensemble. Ezekiel se reposait, il avait passé la nuit à s’entraîner pour mieux maîtriser ses pouvoirs. Tant dit que moi, j’aidais Thomas à mieux contrôler la poussée de ses os. À chaque fois qu’il arrêtait de se concentrer, des os lui poussaient dans le dos ou les bras. Il ne savait pas encore ignorer la douleur ou simplement contrôler son pouvoir. Au moins, les endroits où les os poussaient, la peau se refermait doucement.</p>\n<p>Wamien : Aller Thomas, tu es capable, ressent où les os essayent de pousser et retient-les. Je sais que tu en es capable.<br>\nThomas : J’essaie, mais la douleur.<br>\nWamien : Apprivoise la douleur, comme avec un animal de compagnie qui veut te tenir tête. Montre-lui qui est le boss dans ton corps.</p>\n<p>Thomas tremblait. Il avait mal, mais j’étais dur avec lui, simplement parce que je savais qu’il en était capable et qu’il ne devait pas laisser la douleur le surpasser. Thomas retient un cri de douleur et trois longs piques sortirent de son dos, elles avaient poussées depuis sa colonne vertébrale.</p>\n<p>Wamien : Ça va Thomas?<br>\nThomas : Ça fait super mal…<br>\nWamien : Ok, ouvre les yeux et écoute-moi</p>\n<p>Thomas ouvrit les yeux et me regarda.</p>\n<p>Wamien : On change de stratégie, laisse sortir tous les os qui veulent sortir de toi d’accord?<br>\nThomas : Ça fait déjà assez mal juste trois os comme ça, je vais mourir de douleur si j’en laisse sortir plus!<br>\nWamien : Fais-moi confiance, le but est que tu réussisses à les sortir sans avoir mal et surtout à savoir les rétracter quand tu n’en as plus besoin.</p>\n<p>Thomas hocha la tête et inspira un bon coup avant de laisser sortir plusieurs os au niveau de ses bras de son dos et quelques os poussèrent au niveau de sa tête. Thomas serrait les dents en pleurant.</p>\n<p>Wamien : Inspire et expire. Laisse ton pouvoir se défouler.</p>\n<p>Thomas s’efforçait de respirer du mieux qu’il pouvait, mais la douleur l’empêchait de bien respirer.</p>\n<p>Wamien : Maintenant, concentre-toi sur les os qui ont poussé sur ta tête. Tu penses que tu as besoin de protéger ta tête?<br>\nThomas : N... non.<br>\nWamien : Alors, dis-leur que tout va bien.<br>\nThomas : Tout va bien… Tout va bien…</p>\n<p>Je souris en constatant que les os au niveau de la tête rentrent doucement.</p>\n<p>Wamien : Même chose avec tes bras, tu as besoin de te défendre de la douleur?<br>\nThomas : Non, je dois la combattre…<br>\nWamien : Hé bien, c’est avec ton cerveau et ton cœur que tu dois combattre la douleur. Pas avec ses bras, alors dit à tes bras, qu’ils n’ont pas besoin de se battre.<br>\nThomas : Compris, même chose pour mon dos?<br>\nWamien : Même chose pour ton dos.</p>\n<p>Thomas s’exécuta, rétractant les os de ses bras et un peu après les os dans son dos.</p>\n<p>Wamien : Maintenant, quand tu en as besoin, tu sais que tu peux les sortir et les ranger comme tu le souhaites.</p>\n<p>Thomas hocha la tête en souriant et en essuyant les larmes qui avaient coulé sur ses joues. Je vais reprendre le chandail à Thomas pour lui redonner. Il le remit et replaça ses cheveux.</p>\n<p>Thomas : Merci Wamien.<br>\nWamien : Ça fait plaisir. On va rejoindre les autres, votre fête va bientôt commencer.<br>\nThomas : D’accord.</p>\n<p>Il y alla en souriant, content d’avoir appris à contrôler son pouvoir simplement en restant calme et détendus. Je le suis et on rentre dans la cuisine où les plats avaient été préparés. Sarah et Gaïa se mettent à crier scandale et Sarah se dépêche de nous faire sortir de la pièce.</p>\n<p>Sarah : Ne fais pas gâcher la surprise!<br>\nWamien : Ça va, ça va. On ne faisait que passer voyons.<br>\nSarah : Ouais c’est ça!</p>\n<p>Elle rigole un peu et ferme doucement la porte.</p>\n<p>Quelques heures plus tard, tout le monde était là et la fête commençait. Il y avait toute sorte de plats que les filles avaient préparés et tous se servaient. Je souriais, content de voir que tout le monde s’entendait bien. Un peu plus tard dans la soirée, je me place au centre de la pièce.</p>\n<p>Wamien : Je voudrais souhaiter encore une fois la bienvenue des nouveaux membres de La Guilde! Votre seconde famille ou nouvelle selon ce que vous souhaitez. Vous êtes les bienvenus parmi nous et je veux que vous vous améliorez dans le contrôle de vos pouvoirs tout en faisant le bien autour de vous!</p>\n<p>Tous crièrent et applaudirent mon petit discours et Hiro retourna montrer la musique.</p>\n<p>La fête finit vers 22 h et on commençait à ranger et nettoyer l’endroit. Plusieurs étant parti dormir. Les prochains jours allaient être calme pour La Guilde, sauf pour Wamien qui continuait à rechercher des indices sur où se trouvaient les Black Eagles et La Légion.</p>"}},"chapitreAvant":"chapitre-8-sans-succes","chapitreApres":"chapitre-10-william","nomRoman":"le-premier-cyborg-tome-2","slug":"chapitre-9-nouvelle-famille","codeChapitre":"H0002C009","equivalentUrl":"/stories/chapter/chapter-9-new-familly"}},"pathContext":{"slug":"chapitre-9-nouvelle-famille"}}

